* {
  margin: 0;
  font-family: 'Nunito Sans', sans-serif;
  color: #111517;
  text-decoration: none;
}

.inputs {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-between;
  margin: 5%;
}

input,
select {
  border: 0;
  box-shadow: 1px 1px 1px 1px rgba(211, 211, 211, 0.3);
  width: 250px;
  height: 50px;
  border-radius: 2px;
}

input,
select:focus {
  outline: none;
}

::-webkit-input-placeholder {
  color: #111517;
}

:-ms-input-placeholder {
  color: #111517;
}

::-ms-input-placeholder {
  color: #111517;
}

::placeholder {
  color: #111517;
}

@media screen and (max-width: 560px) {
  .inputs {
    justify-content: center;
  }
  input,
  select {
    margin: 10px;
  }
}
