* {
  font-family: 'Nunito Sans', sans-serif;
  color: #111517;
  text-decoration: none;
}

img {
  width: 250px;
  border-top-left-radius: 4px;
  border-top-right-radius: 4px;
}

.country {
  margin: 5px;
  cursor: pointer;
}

.country span {
  margin: 10px;
  font-weight: 600;
}

.list {
  background-color: white;
  box-shadow: 1px 1px 1px 1px rgba(211, 211, 211, 0.3);
  border-radius: 4px;
