* {
  margin: 0;
  font-family: 'Nunito Sans', sans-serif;
  color: #111517;
  text-decoration: none;
}

button:focus {
  outline: none;
}

button {
  margin: 10px;
  cursor: pointer;
  font-weight: 800;
  background-color: #fff;
  border: 0;
  width: 150px;
  border-radius: 2px;
  height: 30px;
  box-shadow: 2px 2px rgba(163, 163, 163, 0.3);
}

.left {
  margin: 10px;
}

.left img {
  width: 400px;
}

.info {
